#header{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content:center;
    width: 100%;
    height: 100vh;
    background-color: #011C27; 
    /*background-color: #aaaaaa;*/
    /*color:#62BEC1;*/
    color:#62BEC1;
}   
.container{
    text-align: left;
    width: fit-content;
    font-size: xx-large;
    margin-left: 0 auto;
    position: relative;
    border-left: #62BEC1 3px solid ;
    border-top: #62BEC1 2px solid ;
    transition: 2s ease;
    padding:0 10px;
    
}
.container:hover{
    
    color:#D81159;
}
.container h1{
    font-weight: 300px;
}
#links{
    display: flex;
    justify-content: space-evenly;
    list-style: none;
    font-size: 1.5em;
    width: auto;
    
}
#links li{
    padding:0 5px;
}
#links li a{
    color: #62BEC1;
    transition: color 1s ease;
    &:hover{
        color:#D81159
    }
}

@media (max-width: 767px) {
    .container{
        text-align: left;
        width: fit-content;
        font-size: large;
        margin-left: 20px;
        position: relative;
        border-left: #D81159 3px solid ;
        border-top: #D81159 2px solid ;
        transition: 2s ease;
    }
  }